<template>
	<view class="list_comment">
		<view class="item_comment" v-for="(o, i) in list" :key="i" :to="'/pages/article/details?' + vm.comment_id + '=' + o[vm.comment_id]">
			<view class="left_block">
				<image style="width: 2rem;height: 2rem;border-radius: 4px" :src="$fullUrl(o[vm.avatar]) || '/static/img/avatar.jpg'"></image>
			</view>
			<view class="right_block">
				<view class="top_comment">
					<view class="nickname">
						{{o[vm.nickname]}}
					</view>
				</view>
				<rich-text class="content" :nodes="o[vm.content]"></rich-text>
				
				<view class="time">
					{{o[vm.create_time]}}
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		props: {
			list: {
				type: Array,
				default: function() {
					return [];
				}
			},
			vm: {
				type: Object,
				default: function() {
					return {
						avatar: "avatar",
						nickname: "nickname",
						comment_id: "comment_id",
						create_time: "create_time",
						content: "content"
					}
				}
			}
		}
	}
</script>

<style scoped>
	.list_comment {
		padding: 0 0.6rem;
	}

	.item_comment {
		display: flex;
		padding: 0.5rem 0;
	}

	.item_comment+.item_comment {
		border-top: 1px solid #DBDBDB;
	}

	.left_block {
		display: inline-block;
		padding: 0.3rem;
		padding-right: 0.5rem;
	}

	.right_block {
		display: inline-block;
		width: calc(100% - 2.5rem);
	}

	.time {
		color: #999;
	}

	.top_comment {
		margin-bottom: 0.6rem;
	}

	.time {
		text-align: right;
		margin-top: 3px;
		font-size: 12px;
		position: relative;
		left: 1.5rem;
		transform: scale(0.8);
	}

	.top_comment .nickname {
		color: var(--color_grey);
		font-size: 0.8rem;
	}

	.content {
		font-size: 0.8rem;
	}
</style>
